.desktop-home {
    height: calc(100vh - 55px - 32px);
    background-color: #fafafa;
    display: flex;
    flex-direction: column;
    padding: 0.5rem 1rem 1rem;
}

.desktop-home>*:last-child {
    flex: 1;
    flex-grow: 1;
    /* overflow: auto; */
}

.dh-stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(170px, 1fr));
    grid-gap: 0.5rem;
    margin: 0.5rem 0 0.5rem;
}

.dh-content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(25%, 1fr));
    grid-gap: 1rem;
    flex: 1;
}

.dh-column {
    position: relative;
    border-radius: 5px;
    background-color: #fff;
    border: 1px solid #dedede;
    height: 100%;
    padding: 1rem;
    padding-top: 0;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.dh-column .dh-lane-header {
    background-color: #fff;
    margin-left: -1rem;
    margin-right: -1rem;
    padding-left: 1rem;
    padding-right: 1rem;
}

.dh-column.from-widget {
    height: 100%;
    border-radius: 0;
    background-color: #fafafa;
    border: 1px solid #fafafa;
}

.dh-column.from-widget .dh-lane-header {
    background-color: #fafafa;
}

.dh-column.primary,
.dh-column.primary .dh-lane-header {
    background-color: #b0c2c2;
}

.dh-column .dh-lane-content {
    flex: 1;
    flex-grow: 1;
}

.dh-project-card {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 12px;
    margin-bottom: 8px;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
    transition: all 100ms linear;
}

.dh-project-card:hover {
    background-color: #007bff10;
    border: 1px solid #007bff10;
}

.dh-project-card.selected {
    background-color: #007bff15;
    border: 1px solid #007bff80;
}

.dh-project-card .pc-avatar {
    width: 40px;
    height: 40px;
    background-color: #fafafa;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    line-height: 40px;
    font-size: 24px;
    color: #333333;
    font-weight: bold;
    text-align: center;
    margin-right: 10px;
    text-transform: uppercase;
}

.dh-project-card .pc-content {
    flex: 1 1;
    overflow: hidden;
}

.dh-project-card .pc-header {
    display: flex;
    align-items: center;
}

.dh-project-card .pc-project-name {
    font-weight: bold;
    color: #333333;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.8;
    white-space: nowrap;
    flex: 1;
    margin: 0;
}

.dh-project-card .pc-worked-time {
    width: fit-content;
    font-size: smaller;
}

.dh-project-card .pc-project-status {
    color: #333333;
    font-size: smaller;
    float: left;
}

.dh-project-card .pc-project-status.selected {
    color: #007bff;
}

.dh-project-card .pc-task-count {
    color: #333333;
    font-size: smaller;
    float: right;
    text-align: right;
}

.dh-project-card .pc-arrow {
    font-size: medium;
    margin-bottom: 1px;
    margin-right: -5px;
}

.dh-timeline-card {
    position: relative;
    background-color: #ffffff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    margin-bottom: 5px;
    padding: 10px 12px;
}

.dh-timeline-card:hover {
    background-color: #007bff10;
}

.dh-timeline-card.is-new {
    background-color: #ffffff70;
    border: 2px dashed rgba(35, 24, 24, 0.5);
}

.dh-timeline-card .tc-add-task,
.dh-timeline-card .tc-task-actions,
.tsk-new-actions {
    position: absolute;
    right: 0;
    z-index: 1;
}

.dh-timeline-card .tc-add-task {
    left: 0;
    width: 100%;
    bottom: -12.5px;
}

.dh-timeline-card .tc-add-task button {
    border: 1px solid #dedede;
    margin: auto;
    padding: 0.15rem;
}

.dh-timeline-card .tc-task-actions,
.tsk-new-actions {
    top: 0;
    height: 100%;
    right: -12.5px;
}

.dh-timeline-card .tc-task-actions>*,
.tsk-new-actions>* {
    display: flex;
    margin: 2.5px 0;
    padding: 0.15rem;
    border: 1px solid #dedede;
}

.dh-timeline-card .tc-task-actions>*,
.tc-task-actions.single>*:first-child {
    transform: translateY(calc(50% + 10px));
}

.dh-timeline-card .tc-data,
.dh-timeline-card .tc-meta {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    line-height: 1.8;
    flex: 1;
}

.dh-timeline-card .tc-meta {
    font-size: small;
    margin-bottom: 5px;
}

.dh-timeline-card .tc-meta>*:not(:first-child) {
    width: fit-content;
    display: inline-flex;
    align-items: center;
}

.dh-timeline-card .tc-meta>*:not(:last-child):not(:first-child) {
    margin-right: 10px;
}

.dh-timeline-card .tc-meta>*>*:first-child {
    margin-right: 2px;
}

.dh-timeline-card .tc-task-count,
.dh-timeline-card .tc-task-name,
.dh-timeline-card .tc-project-name {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #333333;
    margin: 0;
}

.dh-timeline-card .tc-project-name {
    text-align: right;
}

.dh-timeline-header {
    color: #333333;
    font-weight: bold;
    font-size: 0.9rem;
    line-height: 2;
    margin: 6px 0 6px;
}

.dh-timeline-header:not(:first-of-type) {
    margin-top: 12px;
}

.dh-lane-header {
    display: flex;
    flex-wrap: nowrap;
    color: #333333;
    padding: 1rem 0;
    position: sticky;
    top: 0;
    z-index: 3;
}

.dh-lane-header.my-timeline {
    padding: 1rem 1rem 0.5rem 1rem;
    margin: 0 -1rem;
}

.dh-lane-header .lh-main {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.dh-lane-header>*:not(.lh-main) {
    width: fit-content;
}

.lh-title,
.lh-subtitle {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
}

.lh-subtitle {
    color: #6c757d;
    font-size: 0.9rem;
}

.lh-title {
    font-weight: 500;
}

.lh-cta,
.lh-back {
    cursor: pointer;
}

.dh-lh-tasks-cta {
    border-radius: 5px;
    color: #333333;
    display: inline-flex;
    align-items: center;
}

.dh-lh-tasks-cta svg {
    margin: 0 2px 2px -2px;
    font-size: 1rem;
}

.dh-task-card {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 10px 12px;
    margin-bottom: 8px;
    position: relative;
    transition: all 200ms ease;
}

.dh-task-card-active-wrapper:not(.active) .dh-task-card:hover {
    background-color: #007bff10;
}

.dh-task-card.active {
    margin-bottom: 14px;
}

.dh-task-card .tsk-header {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
}

.tsk-header .tsk-task-name {
    color: #333333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 0.95;
    margin: 0;
}

.dh-task-card.active .tsk-task-name {
    font-weight: bold;
}

.tsk-header .tsk-task-status {
    background: none;
    cursor: pointer;
    border: none;
    width: fit-content;
    font-size: smaller;
    margin: 0;
    padding: 0;
}

.dh-task-card .tsk-footer {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    color: #333333;
    margin-top: 5px;
}

.dh-task-card.active .tsk-footer {
    position: absolute;
    justify-content: center;
    left: 0;
    width: 100%;
    bottom: -14px;
}

.dh-task-card.active .tsk-footer>button {
    margin: 0 10px;
}

.dh-task-card-active-wrapper {
    transition: padding-top 0.2s linear, padding-bottom 0.2s linear;
}

.dh-task-card-active-wrapper.active {
    background-color: #c3e7b7;
    border: 2px solid #28a745;
    margin-bottom: 8px;
    padding: 12px;
    border-radius: 5px;
    transition: background-color 0.2s linear, padding-top 0.2s linear, padding-bottom 0.2s linear;
}

.dh-task-card-active-wrapper.active.start {
    background-color: #c3e7b7;
    border: 2px solid #28a745;
}

.dh-task-card-active-wrapper.active.break {
    background-color: #fff4d3;
    border: 2px solid #facb3d;
}

.tsk-content {
    font-weight: 900;
    font-size: 30px;
    text-align: center;
    line-height: 2;
    margin-bottom: 0.75rem;
    color: #28a745;
}

.tsk-content.start {
    color: #28a745;
}

.tsk-content.break {
    color: #ffc107;
}

.tsk-footer>button {
    font-weight: bold;
    border-radius: 5px;
}

.tsk-footer .tsk-task-time {
    font-size: smaller;
}

.dh-add-task-message {
    text-align: center;
    border-radius: 5px;
    border-style: dashed;
    font-size: smaller;
}

.dh-task-breaks {
    background-color: #b0c2c2;
    padding: 12px;
    margin: 0 -1rem -1rem;
    /* position: absolute;
  bottom: 0;
  width: 100%; */
    position: sticky;
    bottom: -1rem;
    z-index: 3;
}

.dh-tb-header {
    font-weight: bold;
    color: #333333;
    margin: 0;
    margin-bottom: 8px;
}

.dh-tb-content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(75px, 1fr));
    grid-gap: 8px;
}

.dh-tb-content .dh-tb-item {
    background-color: #fafafa;
    border-radius: 5px;
    padding: 10px;
    padding-bottom: 8px;
    color: #333333;
}

.dh-tb-item h6 {
    font-size: small;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 0.25rem;
    font-weight: bold;
}

.dh-tb-item p {
    font-size: smaller;
    margin: 0;
}

.dh-sg-item {
    text-align: center;
    padding: 16px 12px;
    background: #fff;
    border: 1px solid #ededed;
    border-radius: 5px;
    height: fit-content;
}

.dh-sg-item h6 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0;
    font-size: 1.5rem;
    font-weight: bold;
}

.dh-sg-item h6.small {
    font-size: 1.25rem;
}

.dh-sg-item p {
    font-size: smaller;
    margin: 0;
    margin-bottom: 0.25rem;
}

.dh-task-active-text {
    color: #009b37;
    margin: -7px 0 5px;
    text-align: center;
    font-size: smaller;
}

.tc-meta select {
    max-width: 40px;
    margin-left: 2px;
    display: inline;
    padding: 5px;
}

.desktop-app-header {
    position: sticky;
    top: 0;
    background: #fff;
    display: flex;
    justify-content: center;
    padding: 0.5rem 0.5rem;
    z-index: 999;
    -webkit-user-select: none;
    border-bottom: 1px solid #00000005;
}

.desktop-app-header>*:nth-child(2) {
    flex: 1;
    color: #6c757d;
    font-size: small;
    text-align: center;
    text-transform: uppercase;
    /* -webkit-app-region: drag; */
}

.desktop-app-header>*:last-child {
    width: 50px;
    /* -webkit-app-region: drag; */
}

.draggable {
    -webkit-app-region: drag;
}

.desktop-app-header .mac-traffic-lights,
.desktop-app-header .traffic-lights {
    display: flex;
    align-items: center;
    width: 50px;
}

.desktop-app-header .traffic-lights {
    width: 40px;
}

.desktop-app-header .mac-traffic-lights {
    justify-content: space-between;
}

.desktop-app-header .traffic-lights {
    margin-top: -0.25rem;
    margin-bottom: -0.25rem;
    margin-right: -0.25rem;
}

.desktop-app-header .traffic-lights .light {
    width: calc(40px / 2);
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #79797a;
    border-radius: 50%;
    padding: 5px;
    cursor: pointer;
    transition: all 0.5ms ease;
}

.desktop-app-header .traffic-lights .light svg {
    cursor: pointer;
}

.desktop-app-header .mac-traffic-lights .light {
    width: 12px;
    height: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: 12px;
}

.desktop-app-header .traffic-lights .light:hover {
    background-color: #00000010;
    color: #6c757d;
}

.desktop-app-header .mac-traffic-lights .light#close {
    background-image: url('data:image/png;base64,')
}

.desktop-app-header .mac-traffic-lights:hover .light#close {
    background-image: url('data:image/png;base64,')
}

.desktop-app-header .mac-traffic-lights .light#min {
    background-image: url('data:image/png;base64,')
}

.desktop-app-header .mac-traffic-lights:hover .light#min {
    background-image: url('data:image/png;base64,')
}

.desktop-app-header .mac-traffic-lights .light#fullscreen {
    background-image: url('data:image/png;base64,')
}

.desktop-app-header .mac-traffic-lights:hover .light#fullscreen {
    background-image: url('data:image/png;base64,')
}